import * as React from "react";
import { useState, useEffect } from "react";
import "./FosterForm.css";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Alert from "react-bootstrap/Alert";

const FosterForm = () => {
  useEffect(() => {
    initializeForm();
  }, []);

  function initializeForm(): void {
    const signatureDate = document.getElementsByName(
      "SignatureDate"
    )[0] as HTMLInputElement;
    signatureDate.value = new Date().toLocaleDateString("en-US");
    setFosterInfo({ ...fosterInfo, ["SignatureDate"]: signatureDate.value });
  }

  const [fosterInfo, setFosterInfo] = useState({
    ContactName: "",
    StreetAddress: "",
    City: "",
    State: "",
    Zip: "",
    Phone: "",
    EmailAddress: "",
    Occupation: "",
    ResidenceType: "",
    MovingResidence: "",
    LandlordName: "",
    LandlordPhone: "",
    OverTwentyOne: "",
    AgeGroupAdults: "",
    AgeGroupTeens: "",
    AgeGroupChildren: "",
    AgeGroupToddlers: "",
    AgeGroupBabies: "",
    ChildrenCatExposure: "",
    Allergies: "",
    AgeGroupAdultCat1yrPlus: "",
    AgeGroupMotherWithKittens: "",
    AgeGroupOrphanedNewbornKittens: "",
    AgeGroupMatureCat8YearPlus: "",
    AgeGroupKittensAge5To12Weeks: "",
    CatCareNeed: "",
    FosterDuration: "",
    HoursFosterCatAlone: "",
    HouseCatInBedOrBath: "",
    TransportFosterCat: "",
    ReliableVehicle: "",
    ExperienceFosterCats: "",
    FosterForAnotherRescue: "",
    ListCompanionAnimals: "",
    CompanionAnimalsSpayedNeutered: "",
    CompanionAnimalsVaccinated: "",
    CompanionAnimalsVaccinationRecords: "",
    HearAboutUs: "",
    VetName: "",
    VetPracticeName: "",
    VetStreetAddress: "",
    VetCity: "",
    VetState: "",
    VetZip: "",
    VetPhone: "",
    PersonalReferenceName1: "",
    PersonalReferenceRelationship1: "",
    PersonalReferencePhone1: "",
    PersonalReferenceName2: "",
    PersonalReferenceRelationship2: "",
    PersonalReferencePhone2: "",
    CertifyInformation: "",
    SignatureTypeFullName: "",
    SignatureDate: "",
    HoneyPot: "",
  });

  const [response, setResponse] = useState({
    type: "",
    message: "",
  });

  const [validated, setValidated] = useState(false);

  function scrollToFirstValidationError(): void {
    var invalidElements = document.querySelectorAll(
      "input.form-control:invalid, input.form-check-input:invalid"
    );
    if (invalidElements.length > 0) {
      invalidElements[0].scrollIntoView({
        behavior: "smooth",
        block: "center",
      });
    }
  }

  const handleChange = (e: any) =>
    setFosterInfo({ ...fosterInfo, [e.target.name]: e.target.value });

  const handleSubmit = async (e: any) => {
    e.preventDefault();
    if (e.currentTarget.checkValidity() === true) {
      try {
        const button = document.getElementById(
          "submit-button"
        ) as HTMLButtonElement;
        button.disabled = true;
        button.innerHTML = "Please wait...";
        const res = await fetch(
          "https://api.finnsfureverfelinerescue.org/api/foster",
          {
            method: "POST",
            body: JSON.stringify(fosterInfo),
            headers: { "Content-Type": "application/json" },
          }
        );

        if (res.status === 200) {
          setResponse({
            type: "success",
            message:
              'Your form has been submitted successfully. You should receive a confirmation email shortly. If you do not receive it within a few minutes please check your "spam" or "junk" folder. Thank you for your interest in adopting from us!',
          });
        } else {
          setResponse({
            type: "error",
            message:
              "There was an error submitting your form. Please try again later or reach out to us using the contact email.",
          });
        }
      } catch (e) {
        console.log("An error occurred", e);
        setResponse({
          type: "error",
          message:
            "There was an error submitting your form. Please try again later or reach out to us using the contact email.",
        });
      }
    } else {
      scrollToFirstValidationError();
    }
    setValidated(true);
  };

  return (
    <Container className="foster-form-container">
      <Row>
        <Col sm={2}></Col>
        <Col sm={8}>
          <h2>Foster Application</h2>
        </Col>
        <Col sm={2}></Col>
      </Row>
      <Row>
        <Col sm={2}></Col>
        <Col sm={8}>
          <Row className={response.type === "success" ? "" : "hidden"}>
            <Alert variant="success" className="alert-message">
              {response.message}
            </Alert>
          </Row>
          <Row className={response.type === "error" ? "" : "hidden"}>
            <Alert variant="danger" className="alert-message">
              {response.message}
            </Alert>
          </Row>
        </Col>
        <Col sm={2}></Col>
      </Row>
      <Row>
        <Col sm={2}></Col>
        <Col sm={8} className={response.message !== "" ? "hidden" : ""}>
          <Form
            noValidate
            validated={validated}
            method="post"
            onSubmit={handleSubmit}
          >
            <Container>
              <Row>
                <Col
                  sm={12}
                  className="special-reminder-color special-reminder-bottom-space"
                >
                  {/* *** ALL fields are required *** */}
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Contact Information</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="ContactName">
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type="text"
                      name="ContactName"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="StreetAddress">
                    <Form.Label>Street Address</Form.Label>
                    <Form.Control
                      type="text"
                      name="StreetAddress"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={6}>
                  <Form.Group className="mb-3" controlId="City">
                    <Form.Label>City</Form.Label>
                    <Form.Control
                      type="text"
                      name="City"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={3}>
                  <Form.Group className="mb-3" controlId="State">
                    <Form.Label>State</Form.Label>
                    <Form.Select
                      name="State"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="AL">AL</option>
                      <option value="AK">AK</option>
                      <option value="AR">AR</option>
                      <option value="AZ">AZ</option>
                      <option value="CA">CA</option>
                      <option value="CO">CO</option>
                      <option value="CT">CT</option>
                      <option value="DC">DC</option>
                      <option value="DE">DE</option>
                      <option value="FL">FL</option>
                      <option value="GA">GA</option>
                      <option value="HI">HI</option>
                      <option value="IA">IA</option>
                      <option value="ID">ID</option>
                      <option value="IL">IL</option>
                      <option value="IN">IN</option>
                      <option value="KS">KS</option>
                      <option value="KY">KY</option>
                      <option value="LA">LA</option>
                      <option value="MA">MA</option>
                      <option value="MD">MD</option>
                      <option value="ME">ME</option>
                      <option value="MI">MI</option>
                      <option value="MN">MN</option>
                      <option value="MO">MO</option>
                      <option value="MS">MS</option>
                      <option value="MT">MT</option>
                      <option value="NC">NC</option>
                      <option value="NE">NE</option>
                      <option value="NH">NH</option>
                      <option value="NJ">NJ</option>
                      <option value="NM">NM</option>
                      <option value="NV">NV</option>
                      <option value="NY">NY</option>
                      <option value="ND">ND</option>
                      <option value="OH">OH</option>
                      <option value="OK">OK</option>
                      <option value="OR">OR</option>
                      <option value="PA">PA</option>
                      <option value="RI">RI</option>
                      <option value="SC">SC</option>
                      <option value="SD">SD</option>
                      <option value="TN">TN</option>
                      <option value="TX">TX</option>
                      <option value="UT">UT</option>
                      <option value="VT">VT</option>
                      <option value="VA">VA</option>
                      <option value="WA">WA</option>
                      <option value="WI">WI</option>
                      <option value="WV">WV</option>
                      <option value="WY">WY</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={3}>
                  <Form.Group className="mb-3" controlId="Zip">
                    <Form.Label>Zip</Form.Label>
                    <Form.Control
                      type="text"
                      name="Zip"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="Phone">
                    <Form.Label>Phone</Form.Label>
                    <Form.Control
                      type="tel"
                      name="Phone"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="EmailAddress">
                    <Form.Label>Email Address</Form.Label>
                    <Form.Control
                      type="email"
                      name="EmailAddress"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required and needs to be a valid email
                      address.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="Occupation">
                    <Form.Label>Occupation</Form.Label>
                    <Form.Control
                      type="text"
                      name="Occupation"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Household Information</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={6}>
                  <Form.Group className="mb-3" controlId="ResidenceType">
                    <Form.Label>Type of Residence</Form.Label>
                    <Form.Select
                      name="ResidenceType"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="House (own)">House (own)</option>
                      <option value="House (rent)">House (rent)</option>
                      <option value="Apartment">Apartment</option>
                      <option value="Condo">Condo</option>
                      <option value="Mobile Home">Mobile Home</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={6}>
                  <Form.Group className="mb-3" controlId="MovingResidence">
                    <Form.Label>
                      Are you planning to move within the next year?
                    </Form.Label>
                    <Form.Select
                      name="MovingResidence"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                      <option value="Unsure">Unsure</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="LandlordName">
                    <Form.Label>Landlord's Name (if applicable)</Form.Label>
                    <Form.Control
                      type="text"
                      name="LandlordName"
                      placeholder=""
                      onChange={handleChange}
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="LandlordPhone">
                    <Form.Label>
                      Landlord's Phone (if applicable)
                    </Form.Label>
                    <Form.Control
                      type="tel"
                      name="LandlordPhone"
                      placeholder=""
                      onChange={handleChange}
                    />
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="OverTwentyOne">
                    <Form.Label>Are you over 21 years old?</Form.Label>
                    <Form.Select
                      name="OverTwentyOne"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="AgeGroupsInHome">
                    <Form.Label>Number of each in the home:</Form.Label>
                    <Row>
                      <Col sm={6}>
                        <Form.Group
                          as={Row}
                          className="mb-3"
                          controlId="AgeGroupAdults"
                        >
                          <Form.Label column sm="7">
                            Adults
                          </Form.Label>
                          <Col sm={5}>
                            <Form.Control
                              type="text"
                              name="AgeGroupAdults"
                              placeholder=""
                              defaultValue="0"
                              onChange={handleChange}
                              required
                            />
                          </Col>
                          <Form.Control.Feedback type="invalid">
                            This field is required.
                          </Form.Control.Feedback>
                        </Form.Group>
                      </Col>
                      <Col sm={6}>
                        <Form.Group
                          as={Row}
                          className="mb-3"
                          controlId="AgeGroupTeens"
                        >
                          <Form.Label column sm="7">
                            Teens (ages 12-18)
                          </Form.Label>
                          <Col sm={5}>
                            <Form.Control
                              type="text"
                              name="AgeGroupTeens"
                              placeholder=""
                              defaultValue="0"
                              onChange={handleChange}
                              required
                            />
                          </Col>
                          <Form.Control.Feedback type="invalid">
                            This field is required.
                          </Form.Control.Feedback>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={6}>
                        <Form.Group
                          as={Row}
                          className="mb-3"
                          controlId="AgeGroupChildren"
                        >
                          <Form.Label column sm="7">
                            Children (ages 4-12)
                          </Form.Label>
                          <Col sm={5}>
                            <Form.Control
                              type="text"
                              name="AgeGroupChildren"
                              placeholder=""
                              defaultValue="0"
                              onChange={handleChange}
                              required
                            />
                          </Col>
                          <Form.Control.Feedback type="invalid">
                            This field is required.
                          </Form.Control.Feedback>
                        </Form.Group>
                      </Col>
                      <Col sm={6}>
                        <Form.Group
                          as={Row}
                          className="mb-3"
                          controlId="AgeGroupToddlers"
                        >
                          <Form.Label column sm="7">
                            Toddlers (ages 2 -4)
                          </Form.Label>
                          <Col sm={5}>
                            <Form.Control
                              type="text"
                              name="AgeGroupToddlers"
                              placeholder=""
                              defaultValue="0"
                              onChange={handleChange}
                              required
                            />
                          </Col>
                          <Form.Control.Feedback type="invalid">
                            This field is required.
                          </Form.Control.Feedback>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={6}>
                        <Form.Group
                          as={Row}
                          className="mb-3"
                          controlId="AgeGroupBabies"
                        >
                          <Form.Label column sm="7">
                            Babies (under 2 years of age)
                          </Form.Label>
                          <Col sm={5}>
                            <Form.Control
                              type="text"
                              name="AgeGroupBabies"
                              placeholder=""
                              defaultValue="0"
                              onChange={handleChange}
                              required
                            />
                          </Col>
                          <Form.Control.Feedback type="invalid">
                            This field is required.
                          </Form.Control.Feedback>
                        </Form.Group>
                      </Col>
                      <Col sm={6}></Col>
                    </Row>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="ChildrenCatExposure">
                    <Form.Label>
                      Have the children been exposed to cats/kittens?
                    </Form.Label>
                    <Form.Select
                      name="ChildrenCatExposure"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="Allergies">
                    <Form.Label>Does anyone have allergies?</Form.Label>
                    <Form.Select
                      name="Allergies"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Fostering Information</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="AgeGroupsFostering">
                    <Form.Label>
                      Which of the following age groups are you interested in
                      fostering? (Select all that apply.)
                    </Form.Label>
                    <Row>
                      <Col sm={12}>
                        <Form.Group
                          as={Row}
                          controlId="AgeGroupAdultCat1YearPlus"
                        >
                          <Col sm={1} className="form-check-container">
                            <Form.Check
                              inline
                              name="AgeGroupAdultCat1yrPlus"
                              onChange={handleChange} 
                            />
                          </Col>
                          <Form.Label column sm="11">
                            Adult cat 1yr+
                          </Form.Label>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={12}>
                        <Form.Group
                          as={Row}
                          controlId="AgeGroupMotherWithKittens"
                        >
                          <Col sm={1} className="form-check-container">
                            <Form.Check
                              inline
                              name="AgeGroupMotherWithKittens"
                              onChange={handleChange}
                            />
                          </Col>
                          <Form.Label column sm="11">
                            Mother with kittens
                          </Form.Label>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={12}>
                        <Form.Group
                          as={Row}
                          controlId="AgeGroupOrphanedNewbornKittens"
                        >
                          <Col sm={1} className="form-check-container">
                            <Form.Check
                              inline
                              name="AgeGroupOrphanedNewbornKittens"
                              onChange={handleChange}
                            />
                          </Col>
                          <Form.Label column sm="11">
                            Orphaned newborn kittens (requires around-the-clock
                            care and supervision)
                          </Form.Label>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={12}>
                        <Form.Group
                          as={Row}
                          controlId="AgeGroupMatureCat8YearPlus"
                        >
                          <Col sm={1} className="form-check-container">
                            <Form.Check
                              inline
                              name="AgeGroupMatureCat8YearPlus"
                              onChange={handleChange}
                            />
                          </Col>
                          <Form.Label column sm="11">
                            Mature cat 8yr+ (could be long-term care)
                          </Form.Label>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Row>
                      <Col sm={12}>
                        <Form.Group
                          as={Row}
                          controlId="AgeGroupKittensAge5To12Weeks"
                        >
                          <Col sm={1} className="form-check-container">
                            <Form.Check
                              inline
                              name="AgeGroupKittensAge5To12Weeks"
                              onChange={handleChange}
                            />
                          </Col>
                          <Form.Label column sm="11">
                            Kittens age 5 to 12 weeks
                          </Form.Label>
                        </Form.Group>
                      </Col>
                    </Row>
                    <Form.Control.Feedback type="invalid">
                      Select at least one.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="CatCareNeed">
                    <Form.Label>
                      Would you be willing to care for a cat that needs
                      medication, healing from surgery or needs time to heal
                      from wounds?
                    </Form.Label>
                    <Form.Select
                      name="CatCareNeed"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="FosterDuration">
                    <Form.Label>
                      How long would you be able to foster a cat or kitten(s)?
                    </Form.Label>
                    <Form.Select
                      name="FosterDuration"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Up to 1-2 weeks">Up to 1-2 weeks</option>
                      <option value="Up to a month">Up to a month</option>
                      <option value="As long as needed">As long as needed</option>
                      <option value="Not sure">Not sure</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="HoursFosterCatAlone">
                    <Form.Label>
                      How many hours of the day will the foster cat/kitten(s) be
                      left alone?
                    </Form.Label>
                    <Form.Select
                      name="HoursFosterCatAlone"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="0-3 hours">0-3 hours</option>
                      <option value="3-5 hours">3-5 hours</option>
                      <option value="5-8 hours">5-8 hours</option>
                      <option value="9+ hours">9+ hours</option>
                      <option value="Other">Other</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="HouseCatInBedOrBath">
                    <Form.Label>
                      Would you be able to house the cat/kitten(s) in a bedroom
                      or bathroom while they are in your care?
                    </Form.Label>
                    <Form.Select
                      name="HouseCatInBedOrBath"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                      <option value="Not sure">Not sure</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="TransportFosterCat">
                    <Form.Label>
                      Are you able to transport the foster cat/kitten(s) for
                      checkups or treatments?
                    </Form.Label>
                    <Form.Select
                      name="TransportFosterCat"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="ReliableVehicle">
                    <Form.Label>Do you have a reliable vehicle?</Form.Label>
                    <Form.Select
                      name="ReliableVehicle"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="ExperienceFosterCats">
                    <Form.Label>
                      Do you have experience with fostering cats?
                    </Form.Label>
                    <Form.Select
                      name="ExperienceFosterCats"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="FosterForAnotherRescue"
                  >
                    <Form.Label>
                      Are you currently fostering for another animal rescue
                      group?
                    </Form.Label>
                    <Form.Select
                      name="FosterForAnotherRescue"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Current Companion Animals</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="ListCompanionAnimals">
                    <Form.Label>
                      Please list ALL your current companion animals. If you own
                      a dog, have they had positive interactions with cats?
                    </Form.Label>
                    <Form.Control
                      as="textarea"
                      name="ListCompanionAnimals"
                      placeholder=""
                      style={{ height: "250px" }}
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="CompanionAnimalsSpayedNeutered"
                  >
                    <Form.Label>Are they spayed or neutered?</Form.Label>
                    <Form.Select
                      name="CompanionAnimalsSpayedNeutered"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="CompanionAnimalsVaccinated"
                  >
                    <Form.Label>
                      Are they all up to date on vaccinations?
                    </Form.Label>
                    <Form.Select
                      name="CompanionAnimalsVaccinated"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="CompanionAnimalsVaccinationRecords"
                  >
                    <Form.Label>
                      Will you be able to provide your current companion
                      animals' vaccination records?
                    </Form.Label>
                    <Form.Select
                      name="CompanionAnimalsVaccinationRecords"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="Yes">Yes</option>
                      <option value="No">No</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="HearAboutUs">
                    <Form.Label>How did you hear about us?</Form.Label>
                    <Form.Control
                      type="text"
                      name="HearAboutUs"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Veterinary Information</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="VetName">
                    <Form.Label>Name of Vet</Form.Label>
                    <Form.Control
                      type="text"
                      name="VetName"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="VetPracticeName">
                    <Form.Label>Name of Practice</Form.Label>
                    <Form.Control
                      type="text"
                      name="VetPracticeName"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="VetStreetAddress">
                    <Form.Label>Street Address</Form.Label>
                    <Form.Control
                      type="text"
                      name="VetStreetAddress"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={6}>
                  <Form.Group className="mb-3" controlId="VetCity">
                    <Form.Label>City</Form.Label>
                    <Form.Control
                      type="text"
                      name="VetCity"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={3}>
                  <Form.Group className="mb-3" controlId="VetState">
                    <Form.Label>State</Form.Label>
                    <Form.Select
                      name="VetState"
                      placeholder=""
                      onChange={handleChange}
                      required
                    >
                      <option value=""></option>
                      <option value="AL">AL</option>
                      <option value="AK">AK</option>
                      <option value="AR">AR</option>
                      <option value="AZ">AZ</option>
                      <option value="CA">CA</option>
                      <option value="CO">CO</option>
                      <option value="CT">CT</option>
                      <option value="DC">DC</option>
                      <option value="DE">DE</option>
                      <option value="FL">FL</option>
                      <option value="GA">GA</option>
                      <option value="HI">HI</option>
                      <option value="IA">IA</option>
                      <option value="ID">ID</option>
                      <option value="IL">IL</option>
                      <option value="IN">IN</option>
                      <option value="KS">KS</option>
                      <option value="KY">KY</option>
                      <option value="LA">LA</option>
                      <option value="MA">MA</option>
                      <option value="MD">MD</option>
                      <option value="ME">ME</option>
                      <option value="MI">MI</option>
                      <option value="MN">MN</option>
                      <option value="MO">MO</option>
                      <option value="MS">MS</option>
                      <option value="MT">MT</option>
                      <option value="NC">NC</option>
                      <option value="NE">NE</option>
                      <option value="NH">NH</option>
                      <option value="NJ">NJ</option>
                      <option value="NM">NM</option>
                      <option value="NV">NV</option>
                      <option value="NY">NY</option>
                      <option value="ND">ND</option>
                      <option value="OH">OH</option>
                      <option value="OK">OK</option>
                      <option value="OR">OR</option>
                      <option value="PA">PA</option>
                      <option value="RI">RI</option>
                      <option value="SC">SC</option>
                      <option value="SD">SD</option>
                      <option value="TN">TN</option>
                      <option value="TX">TX</option>
                      <option value="UT">UT</option>
                      <option value="VT">VT</option>
                      <option value="VA">VA</option>
                      <option value="WA">WA</option>
                      <option value="WI">WI</option>
                      <option value="WV">WV</option>
                      <option value="WY">WY</option>
                    </Form.Select>
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
                <Col sm={3}>
                  <Form.Group className="mb-3" controlId="VetZip">
                    <Form.Label>Zip</Form.Label>
                    <Form.Control
                      type="text"
                      name="VetZip"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="VetPhone">
                    <Form.Label>Phone</Form.Label>
                    <Form.Control
                      type="tel"
                      name="VetPhone"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>
                    Personal Reference 1 (individual who is familiar with how
                    you care for your current companion animals)
                  </h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferenceName1"
                  >
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferenceName1"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferenceRelationship1"
                  >
                    <Form.Label>Relationship</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferenceRelationship1"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferencePhone1"
                  >
                    <Form.Label>Phone</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferencePhone1"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>
                    Personal Reference 2 (individual who is familiar with how
                    you care for your current companion animals)
                  </h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferenceName2"
                  >
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferenceName2"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferenceRelationship2"
                  >
                    <Form.Label>Relationship</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferenceRelationship2"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="PersonalReferencePhone2"
                  >
                    <Form.Label>Phone</Form.Label>
                    <Form.Control
                      type="text"
                      name="PersonalReferencePhone2"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Your Agreement</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12} className="your-agreement">
                  <Form.Group>
                    <Form.Check
                      type="checkbox"
                      name="CertifyInformation"
                      label="I certify that the information I have provided is true, and I realize that any misrepresentation of facts may result in my losing the privilege of fostering cats from Finn's Furever Feline Rescue.  I understand that my request to foster may be denied for any circumstance or situation that could be deemed not in the best interest of the foster cat.  I authorize investigation of all statements in this application.  I also authorize my veterinarian to release any information that would be helpful in determining a suitable foster."
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      Agreement must be acknowledged.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <h3>Your Agreement Signature</h3>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group
                    className="mb-3"
                    controlId="SignatureTypeFullName"
                  >
                    <Form.Label>Signature (type your full name)</Form.Label>
                    <Form.Control
                      type="text"
                      name="SignatureTypeFullName"
                      placeholder=""
                      onChange={handleChange}
                      required
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Group className="mb-3" controlId="SignatureDate">
                    <Form.Label>Date</Form.Label>
                    <Form.Control
                      type="text"
                      name="SignatureDate"
                      placeholder=""
                      onChange={handleChange}
                      required
                      readOnly={true}
                    />
                    <Form.Control.Feedback type="invalid">
                      This field is required.
                    </Form.Control.Feedback>
                  </Form.Group>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Button id="submit-button" variant="secondary" type="submit">
                    Submit
                  </Button>
                </Col>
              </Row>
              <Row>
                <Col sm={12}>
                  <Form.Control
                    type="text"
                    name="HoneyPot"
                    onChange={handleChange}
                    style={{ display: "none" }}
                  />
                </Col>
              </Row>
            </Container>
          </Form>
        </Col>
        <Col sm={2}></Col>
      </Row>
    </Container>
  );
};

export default FosterForm;
